body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.note.new{
  background-color: #67d7cc;

}


.dark-mode{
  background-color: black;
}

.dark-mode h1{
  color: white;
}

textarea{
  border: none;
  resize: none;
  background-color: #67d7cc;
}

textarea:focus{
  outline: none;

}


.header{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.save{
  background-color: coral;
  border: none;
  border-radius: 15px;
  padding: 5px 10px 5px 10px;
}

.save:hover{
  background-color: blue;
  cursor: pointer;
}

.container{
  max-width: 960px;
  margin-left: auto;
  margin-right: auto; 
  padding-right: 15px;
  padding-left:  15px;
  min-height: 100vh;
}
.notes-list{
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));

}

.note{
  background-color: #fef68a;
  margin-bottom: 10px;
  border-radius:  10px;
  padding: 1rem;
  min-height: 170px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap;
}

.delete-icon{
  cursor: pointer;

}

.note-footer{
  display: flex;
  justify-content: space-between;
}


.search{
  background-color: grey;
  display: flex;
  align-items: center;
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 1.5em;

}

.search input{
  background-color: grey;
  border: none;


}

.search input:focus{
  outline: none;
}